.info__slogan {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 461px;
}

.info__text-content {
  max-width: 470px;
  margin: 0 auto;
}

.info__text-content--main {
  max-width: 530px;
}

.info__slogan-bg {
  position: relative;
  text-align: center;
  padding: 6.43rem 1.36rem;
  background-image: url("../../img/info-bg-decor.png"), url("../img/intro_bg.jpg");
  background-size: $desktop-width, cover;
  background-position: center bottom, center bottom;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    padding-top: 5.5rem;
  }
}
